@media only screen and (max-width: 980px) {
	#header .header_top, 
	#header .footer_top,
	#footer .header_top, 
	#footer .footer_top {
		padding: 0 30px;
	}
	#header .header_bottom, 
	#header .footer_bottom, 
	#footer .header_bottom, 
	#footer .footer_bottom {
		padding: 28px 30px 23px;
	}
	#header .header_bottom .contacts .work_time .one_line{
		display: block;
	}
	#main {
		.wrapper_slider {
			.bg {
				padding: 0;
			}
			.slider {
				.text {
					padding: 0 0 0 15px;
				}
				.wrapper_img {
					padding: 0 15px 0 0;
				}
				.slick-dots {
					right: 30px;
				}
			}
		}
		.looking  {
			.wrapper_img {
				display: none;
			}
		}
	}
	#header .header_top .wrapper_nav .nav, 
	#header .footer_top .wrapper_nav .nav, 
	#footer .header_top .wrapper_nav .nav, 
	#footer .footer_top .wrapper_nav .nav {
		display: none;
	}
	#header .header_top .wrapper_nav .mob-menu, 
	#header .footer_top .wrapper_nav .mob-menu, 
	#footer .header_top .wrapper_nav .mob-menu,
	#footer .footer_top .wrapper_nav .mob-menu {
		display: block;
	}
	#main .best_prise .wrapper_best_prise_box, 
	#main .best_prise .wrapper_popular_tovar_box, 
	#main .popular_tovar .wrapper_best_prise_box, 
	#main .popular_tovar .wrapper_popular_tovar_box {
		padding: 0 30px;
	}
	#main .best_prise .wrapper_best_prise_box .best_prise_box, 
	#main .best_prise .wrapper_best_prise_box .popular_tovar_box, 
	#main .best_prise .wrapper_popular_tovar_box .best_prise_box, 
	#main .best_prise .wrapper_popular_tovar_box .popular_tovar_box, 
	#main .popular_tovar .wrapper_best_prise_box .best_prise_box, 
	#main .popular_tovar .wrapper_best_prise_box .popular_tovar_box, 
	#main .popular_tovar .wrapper_popular_tovar_box .best_prise_box, 
	#main .popular_tovar .wrapper_popular_tovar_box .popular_tovar_box {
		max-width: 48%;
		margin: 0 4% 20px 0;
		&:nth-child(2n) {
			margin: 0 0 20px;
		}
	}
	#main .wrapper_guarantee .guarantee .wrapper_guarantee_box {
		padding: 0 30px;
		.guarantee_box {
			width: 47%;
			margin: 0 4% 25px 0;
			&:nth-child(2n) {
				margin: 0 0 25px;
			}
		}
	}
	#main .reviews .wrapper_reviews_box {
		padding: 0 30px;
		text-align: center;
		.reviews_box {
			max-width: 100%;
			margin: 0 0 30px;
			text-align: left;
		}
	}
	#main .wrapper_payment_tovar .payment_tovar {
		padding: 0 30px;
	}
	#main .delivery_tovar {
	    padding: 40px 30px 30px;
	}
	#main .wrapper_about_us .bg {
	    padding: 40px 30px 30px;
	}
	#main .contacnts .wrapper_contacnts_box .contacnts_box {
		width: 100%;
		margin: 0 0 40px;
		&:last-child {
			margin: 0; 
		}
	}
}
@media only screen and (max-width: 800px) {
	#header .header_bottom .logo {
		width: 30%;
	}
	#header .header_bottom .wrapper_social_search {
		width: 39%;
	}
	#main .looking .wrapper_box .box {
		width: 20%;
	}
	#footer {
		.footer_bottom {
			.logo {
				width: 100%;
				text-align: center;
				margin: 0 0 30px;
				float: none;
				.copyright {
					margin: 5px 0 0;
				}
			}
			.wrapper_social_search {
				width: 100%;
				margin: 0 0 25px;
				float: none;
				.social {
					margin: 0;
				}
			}
			.contacts {
			    float: none;
			    width: 100%;
			    max-width: 100%;
			    text-align: center;
			}
		}
	} 
}
@media only screen and (max-width: 760px) {
	#header .header_bottom {
		.logo {
			width: 100%;
			float: none;
			text-align: center;
			margin: 0 0 15px;
			a {
				span {
					display: none;
				}
			}
		}
		.wrapper_social_search {
			display: none;
		}
		.contacts {
			width: 100%;
			text-align: center;
			max-width: 100%;
			.work_time {
				display: none;
			}
			.phone {
				margin: 0 10px 15px 0;
			}
			.phone.last {
				margin: 0 0 15px;
			}
			.callback {
				display: block;
				max-width: 290px;
				border: 2px solid #f67076;
				border-radius: 20px;
				padding: 10px 0;
				margin: 0 auto;
				background: transparent;
				transition: all .3s ease;
				&:hover {
					background: #f67076;
					color: #fff;
					border: 2px solid #f67076;
					transition: all .3s ease;
				}
			}
		}
	}
}
@media only screen and (max-width: 600px) {
	#header .header_bottom .contacts .phone {
		font-size: 14px;
		letter-spacing: -0.7px;
	}
	#main .wrapper_slider { 
    	background: url(../img/bg_slider_mobile.png) no-repeat center bottom, url(../img/bg_pink.jpg) repeat;
		margin: 0 auto 20px;
		.bg {
			padding: 45px 0 50px;
		}
		.slider {
			.text {
				width: 100%;
				float: none;
				margin: 0;
				text-align: center;
				padding: 0 15px;
				max-width: 100%;

			}
			.wrapper_img {
				display: none;
			}
			.slick-dots {
				width: 100%;
				bottom: -85px;
				right: auto;
				li {
					width: 18px;
					height: 18px;
					margin: 0 0 0 10px;
					button:before {
						width: 18px;
						height: 18px;
					}
				}
			}
		}
	}
	#header .header_top, 
	#header .footer_top, 
	#footer .header_top, 
	#footer .footer_top {
		padding: 0 15px;
	}
	#header .header_bottom, 
	#header .footer_bottom, 
	#footer .header_bottom, 
	#footer .footer_bottom {
		padding: 28px 15px 23px;
	}
	#main .looking .wrapper_box .box {
		width: 40%;
		padding: 0 10px;
	}
	#main .best_prise .wrapper_best_prise_box .best_prise_box, 
	#main .best_prise .wrapper_best_prise_box .popular_tovar_box, 
	#main .best_prise .wrapper_popular_tovar_box .best_prise_box, 
	#main .best_prise .wrapper_popular_tovar_box .popular_tovar_box, 
	#main .popular_tovar .wrapper_best_prise_box .best_prise_box, 
	#main .popular_tovar .wrapper_best_prise_box .popular_tovar_box, 
	#main .popular_tovar .wrapper_popular_tovar_box .best_prise_box, 
	#main .popular_tovar .wrapper_popular_tovar_box .popular_tovar_box,
	#main .tovar_block .wrapper_best_prise_box .best_prise_box {
		max-width: 225px;
		margin: 0 auto 20px;
		float: none;
		&:nth-child(2n) {
			margin: 0 auto 20px;
		}
	}
	#main .best_prise .wrapper_best_prise_box, 
	#main .best_prise .wrapper_popular_tovar_box, 
	#main .popular_tovar .wrapper_best_prise_box, 
	#main .popular_tovar .wrapper_popular_tovar_box,
	#main .wrapper_guarantee .guarantee .wrapper_guarantee_box,
	#main .wrapper_payment_tovar .payment_tovar,
	#main .contacnts .wrapper_contacnts_box .contacnts_box {
		padding: 0 15px;
	}
	#main .delivery_tovar,
	#main .wrapper_about_us .bg {
	    padding: 40px 15px 30px;
	} 
	#main .wrapper_guarantee .guarantee .wrapper_guarantee_box .guarantee_box {
		width: 100%;
		margin: 0 0 25px;
	}
	#main .reviews .wrapper_reviews_box {
		padding: 0 15px;
		.reviews_box {
			text-align: center;
			.wrapper_img {
				float: none;
				margin: 0 auto 25px;
			}
			.wrapper_text {
				.wrapper_name_stars {
					.name {
						display: block;
						margin: 0 0 25px;
					}
				}
			}
		}
	}
}  